import React, { useState, useEffect } from 'react'
import LeftSide from './LeftSide'
import RightSide from './RightSide'
import { erc20 } from '@openzeppelin/wizard';
import axios from 'axios';
// import { erc20 } from '@openzeppelin/wizard-cairo';

export default function Main(props) {
    let erc20Json = {}
    
    let mainDivStyle = {
        // border: '1px solid',
        border: 'outset',
        backgroundColor: '#f9fafb',
        borderRadius: "10px",
        // minHeight: "53rem",
        padding: "1rem",
        width: "1000px"
        // minWidth: "32rem"
    }

    let divBoxStyle = {
        display: "flex"
    }

    let btnStyle = {
        float: "right"
    }

    const erc20Func = () => {
        // const finalContract = erc20.print(erc20Json);
        // setErc20Contract(finalContract);
    }

    const [erc20Contract, setErc20Contract] = useState('');
    const [erc20JsonObj,setErc20JsonObj] = useState(null);

    function erc20ContractFun(name, symbol, mint, burn, pause,premint,access) {

        erc20Json = {
            name: name,
            symbol: symbol,
            mintable: mint,
            burnable: burn,
            pausable: pause,
            premint: premint,
            // snapshots:true,
            flashmint:false,
            access:access
        }
        console.log(erc20Json)
        const finalContract = erc20.print(erc20Json);

        setErc20JsonObj(erc20Json);
        setErc20Contract(finalContract);

    }

    const deplooyContract = () => {
        
        axios.post(`https://cf04-103-156-142-234.ngrok.io/get-erc20-contract`, { erc20JsonObj })
            .then(res => {
                props.deploy(res.data.ABI,res.data.bytecode);
            }).catch(e=>{
                console.log('error ',e);
            })
    }

    const copyToClipboard = () => {
        var text_to_copy = document.getElementById("contract").innerHTML;

        if (!navigator.clipboard) {
            // use old commandExec() way
        } else {
            navigator.clipboard.writeText(text_to_copy).then(
                function () {
                    alert("contract copied to clipboard")
                })
                .catch(
                    function () {
                        alert("something went wrong", "warning")
                    });
        }

    }

    useEffect(() => {
        // document.getElementById('erc20Id').click();
    }, [])

    return (
        <>
            <div className="container my-5" style={mainDivStyle}>
                <button type="button" id="erc20Id" onClick={erc20Func} className="btn btn-primary mx-2">ERC20</button>
                {/* <button type="button" id="erc20Id" onClick={erc20Func} className="btn btn-primary mx-2">ERC20</button> */}
                {/* <button style={btnStyle} onClick={copyToClipboard} type="button" className="btn btn-outline-primary  mx-3">Copy to clipboard</button> */}
                <button style={{ float: "right", display: props.isConnected ? 'block' : 'none' }} onClick={deplooyContract} type="button" className="btn btn-outline-primary  mx-3">Deploy</button>
                <div style={divBoxStyle}>
                    <LeftSide erc20ContractFun={erc20ContractFun}></LeftSide>
                    <RightSide contract={erc20Contract} contractAddress={props.contractAddress}></RightSide>
                </div>
            </div>

        </>

    )
}

