import React from 'react';


export default function RightSide(props) {

    let rightDivStyle = {
        border: 'outset',
        backgroundColor: '#f9fafb',
        borderRadius: "10px",
        // minHeight: "51rem",
        padding: "1rem",
        width: "820px"
    }

    const handleOnChange = (event) => {
        console.log('on change ');
    }

    let txtAreaStyle = {
        backgroundColor: '#282c34',
        color: 'white'
        // #c678dd
    }
    let h1Style = {
        fontSize: "0.875rem",
        color: "grey"
    }

    return (
        <>
            <div className="my-3 mx-3" style={rightDivStyle}>
                <div className="mb-3">
                    <h1 style={h1Style}>DEPLOYMENT DETAILS</h1>
                    <div className='my-3'>
                        <h5>Contract Address </h5>
                        <p>{props.contractAddress}</p>
                        <h5>Etherscan Link </h5>
                        <a target="_blank" style={{display:props.contractAddress?'block':'none'}} href={`https://rinkeby.etherscan.io/address/${props.contractAddress}`}>Click here to view transaction</a>
                    </div>

                    {/* <textarea style={txtAreaStyle} value={props.contract} onChange={handleOnChange} className="form-control" id="contract" rows="33"></textarea> */}
                </div>
            </div>
        </>
    )
}



