import React, { useEffect, useState } from 'react'

export default function LeftSide(props) {

  let erc20Name = '';
  let erc20Symbol = '';
  let mint; let burn; let pause;
  let premint; let access;

  let leftDivStyle = {
    border: 'outset',
    backgroundColor: '#f9fafb',
    borderRadius: "10px",
    // minHeight: "15rem",
    padding: "1rem",
    // width: "300px"
  }

  let h1Style = {
    fontSize: "0.875rem",
    color: "grey"
  }

  let divStyle = {
    display: "inline-flex"
  }

  const nameOnChange = (event) => {
    erc20Name = event.target.value;
    erc20Symbol = document.getElementById('symbol').value;
    mint = document.getElementById('mintChk').checked;
    burn = document.getElementById('burnChk').checked;
    pause = document.getElementById('pauseChk').checked;
    premint = document.getElementById('premint').value;
    access = document.querySelector('input[name="accessControl"]:checked').value;

    props.erc20ContractFun(erc20Name, erc20Symbol, mint, burn, pause, premint,access);
  }

  const symbolOnChange = (event) => {
    erc20Name = document.getElementById('name').value;
    erc20Symbol = event.target.value;
    mint = document.getElementById('mintChk').checked;
    burn = document.getElementById('burnChk').checked;
    pause = document.getElementById('pauseChk').checked;
    premint = document.getElementById('premint').value;
    access = document.querySelector('input[name="accessControl"]:checked').value;
    
    props.erc20ContractFun(erc20Name, erc20Symbol, mint, burn, pause, premint,access);

  }

  const mintCheck = (event) => {

    erc20Name = document.getElementById('name').value;
    erc20Symbol = document.getElementById('symbol').value;
    mint = event.target.checked;
    burn = document.getElementById('burnChk').checked;
    pause = document.getElementById('pauseChk').checked;
    premint = document.getElementById('premint').value;
    access = document.querySelector('input[name="accessControl"]:checked').value;

    props.erc20ContractFun(erc20Name, erc20Symbol, mint, burn, pause, premint,access);

  }

  const burnCheck = (event) => {

    erc20Name = document.getElementById('name').value;
    erc20Symbol = document.getElementById('symbol').value;
    mint = document.getElementById('mintChk').checked;
    burn = event.target.checked;
    pause = document.getElementById('pauseChk').checked;
    premint = document.getElementById('premint').value;
    access = document.querySelector('input[name="accessControl"]:checked').value;

    props.erc20ContractFun(erc20Name, erc20Symbol, mint, burn, pause, premint,access);

  }

  const pauseCheck = (event) => {

    erc20Name = document.getElementById('name').value;
    erc20Symbol = document.getElementById('symbol').value;
    mint = document.getElementById('mintChk').checked;
    burn = document.getElementById('burnChk').checked;
    pause = event.target.checked;
    premint = document.getElementById('premint').value;
    access = document.querySelector('input[name="accessControl"]:checked').value;

    props.erc20ContractFun(erc20Name, erc20Symbol, mint, burn, pause, premint,access);

  }

  const premintChange = () => {
    erc20Name = document.getElementById('name').value;
    erc20Symbol = document.getElementById('symbol').value;
    mint = document.getElementById('mintChk').checked;
    burn = document.getElementById('burnChk').checked;
    pause = document.getElementById('pauseChk').checked;
    premint = document.getElementById('premint').value;
    access = document.querySelector('input[name="accessControl"]:checked').value;

    props.erc20ContractFun(erc20Name, erc20Symbol, mint, burn, pause, premint,access);

  }


  const accessControlChange = (event) => {
    erc20Name = document.getElementById('name').value;
    erc20Symbol = document.getElementById('symbol').value;
    mint = document.getElementById('mintChk').checked;
    burn = document.getElementById('burnChk').checked;
    pause = document.getElementById('pauseChk').checked;
    premint = document.getElementById('premint').value;
    access = event.target.value;
    props.erc20ContractFun(erc20Name, erc20Symbol, mint, burn, pause, premint,access);

  }

  useEffect(() => {
    erc20Name = document.getElementById('name').value = "MyToken";
    erc20Symbol = document.getElementById('symbol').value = "MTK";
    premint = document.getElementById('premint').value;
    // access = document.querySelector('input[name="accessControl"]:checked').value;
    props.erc20ContractFun(erc20Name, erc20Symbol, false, false, false, premint,access);

  }, [])


  return (
    <>
      <div className="my-3" style={leftDivStyle}>
        <h1 style={h1Style}>SETTINGS</h1>
        <div style={divStyle}>
          <div className="mx-2">
            <label className="form-label">Name</label>
            <input onChange={nameOnChange} type="text" className="form-control" id="name" />
          </div>
          <div className="mx-2">
            <label className="form-label">Symbol</label>
            <input onChange={symbolOnChange} type="text" className="form-control" id="symbol" />
          </div>
        </div>
        <div className="my-2">
          <label className="form-label">Premint</label>
          <input onChange={premintChange} type="text" className="form-control" id="premint" />
        </div>
        <hr></hr>
         <h1 className="my-2" style={h1Style}>FEATURES</h1>
        <div className="form-check">
          <input onChange={mintCheck} className="form-check-input" type="checkbox" value="" id="mintChk" />
          <label className="form-check-label" >Mintable</label>
        </div>
        <div className="form-check">
          <input onChange={burnCheck} className="form-check-input" type="checkbox" value="" id="burnChk" />
          <label className="form-check-label" >Burnable</label>
        </div>
        <div className="form-check">
          <input onChange={pauseCheck} className="form-check-input" type="checkbox" value="" id="pauseChk" />
          <label className="form-check-label" >Pausable</label>
        </div>
        <hr></hr>
        <h1 className="my-2" style={h1Style}>ACCESS CONTROL</h1>
        <div className="form-check">
          <input onChange={accessControlChange} className="form-check-input" value='ownable' type="radio" name="accessControl" id="ownable" defaultChecked />
          <label className="form-check-label" htmlFor="ownable">
            Ownable
          </label>
        </div>
        <div className="form-check">
          <input onChange={accessControlChange} className="form-check-input" value='roles' type="radio" name="accessControl" id="role" />
          <label className="form-check-label" htmlFor="role">
            Roles
          </label>
        </div> 
      </div>
    </>
  )
}
