import './App.css';
import Main from './Main';
import Navbar from './Navbar';
import { useState, useEffect } from 'react';
import Web3 from "web3";
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
function App() {

  document.body.style.backgroundColor = '#f9fafb';
  const CHAIN_ID = '0x4';

  let chainDeteched = {
    "0x1": "Ethereum Mainnet Network",
    "0x3": "Ropsten Test Network",
    "0x4": "Rinkeby Test Network",
    "0x5": "Goerli Test Network",
    "0x2a": "Kovan Test Network",
    "0x539": "Ganache Network",
  };

  const [walletAddress, setWalletAddress] = useState('');
  const [web3, setWeb3] = useState();
  const [isConnected, setIsConnected] = useState(false);
  const [contractAddress, setContractAddress] = useState('');
  let web3Modal;


  const onConnect = async () => {
    try {
      const provider = await web3Modal.connect();
      const _web3 = initWeb3(provider);
      await subscribeProvider(provider, _web3);
      const chainId = await _web3.eth.chainId();
      if (chainId !== undefined) {
        if (chainId !== CHAIN_ID) {
          console.log(
            "Please Connect to " +
            chainDeteched[CHAIN_ID] +
            " , And try to connect."
          );
        } else {
          const accounts = await _web3.eth.getAccounts();
          const address = accounts[0];
          setIsConnected(true);
          setWalletAddress(address);
          setWeb3(_web3);
          
        }
      }
    } catch (e) {
      setIsConnected(false);
    }

  };

  const subscribeProvider = async (provider, _web3) => {
    
    if (!provider.on) {
      return;
    }
    
    provider.on("close", () => resetApp());
    provider.on("accountsChanged", async (accounts) => {
      setWalletAddress(accounts[0]);
    });
    provider.on("chainChanged", async (chainId) => {
      if (
        chainDeteched[chainId] &&
        chainDeteched[chainId] !== undefined &&
        chainId === CHAIN_ID
      ) {
        // const chainId = await _web3.eth.chainId();
        const accounts = await _web3.eth.getAccounts();
        setWalletAddress(accounts[0]);
      } else {
        console.log(
          "Please Connect to " +
          chainDeteched[CHAIN_ID] +
          " , And try to connect."
        );
      }
    });
  };

  const resetApp = async () => {
    if (web3 && web3.currentProvider && web3.currentProvider.close) {
      await web3.currentProvider.close();
    }
    await web3Modal.clearCachedProvider();

    setWalletAddress("");
    setWeb3(null);
  };

  function initWeb3(provider) {
    const web3 = new Web3(provider);

    web3.eth.extend({
      methods: [
        {
          name: "chainId",
          call: "eth_chainId",
        },
      ],
    });

    return web3;
  }

  useEffect(() => {
    const getProviderOptions = () => {
      const providerOptions = {
        walletconnect: {
          package: WalletConnectProvider,
          options: {
            infuraId: process.env.REACT_APP_INFURA_ID,
          },
        },
      };
      return providerOptions;
    };

    web3Modal = new Web3Modal({
      network: CHAIN_ID,
      cacheProvider: false,
      providerOptions: getProviderOptions(),
    });
  }, []);

  const deploy = async (abi, byteCode) => {

    let contract = new web3.eth.Contract(abi);
    let defaultAccount;

    web3.eth.getAccounts().then((accounts) => {
      console.log("Accounts:", accounts); //it will show all the ganache accounts
      defaultAccount = accounts[0];
      console.log("Default Account:", defaultAccount);  //to deploy the contract from default Account
      contract
        //  .deploy({ data: bytecode })
        .deploy({ data: byteCode })
        // .send({ from: defaultAccount, gas: 470000 })
        .send({ from: defaultAccount })
        .on("receipt", (receipt) => { //event,transactions,contract address will be returned by blockchain
          console.log("Contract Address:", receipt.contractAddress);
          setContractAddress(receipt.contractAddress);
        })
        .then((demoContract) => {
          // demoContract.methods.x().call((err, data) => {
          //   console.log("Initial Value:", data);
          // });
        });

    });
  }

  return (
    <>
      {/* <Metamask /> */}
      {/* <Navbar title="Techforce" /> */}
      <Navbar title="Techforce" walletAddress={walletAddress} onConnect={onConnect} isConnected={isConnected} setIsConnected={setIsConnected} ></Navbar>
      <Main isConnected={isConnected} deploy={deploy} contractAddress={contractAddress}></Main>
    </>
  );
}

export default App;
